.overlay {
    width: 100%;
    height: 100%;
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .1);
    z-index: 1;
    visibility: hidden;
    opacity: .5;
    transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    overflow: hidden;
}

.overlayVisible {
    visibility: visible;
    opacity: 1;

    .drawer {
        transform: translateX(0);
    }
}

.drawer {
    position: absolute;
    width: 420px;
    height: 100%;
    right: 0;
    top: 0;
    background-color: #fff;
    box-shadow: -10px 4px 24px rgba(0, 0, 0, .1);
    padding: 30px;
    display: flex;
    flex-direction: column;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;

    .items {
        flex: 1;
        overflow: auto;
    }
}